.demo-home-nav__title {
  margin: 1.024rem 0 0.34133rem 0.68267rem;
  color: rgba(69, 90, 100, 0.6);
  font-size: 0.59733rem;
}
.demo-home-nav__block {
  position: relative;
  display: -webkit-flex;
  display: flex;
  margin: 0 0 0.512rem;
  padding-left: 0.85333rem;
  color: #323233;
  font-weight: 500;
  font-size: 0.59733rem;
  line-height: 1.70667rem;
  background: #f7f8fa;
  border-radius: 4.224rem;
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
}
.demo-home-nav__block:hover {
  background: #eef0f4;
}
.demo-home-nav__block:active {
  background: #e4e8ee;
}
.demo-home-nav__icon {
  position: absolute !important;
  top: 50%;
  right: 0.68267rem;
  width: 0.68267rem;
  height: 0.68267rem;
  margin-top: -0.34133rem;
  color: #b6c3d2;
  font-weight: 900 !important;
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
